*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}
.card {
  width: 90%;
  height: 100%;
  max-height: 300px;
  padding: auto;
  text-align: center;
  background: white;
  border-radius: 10px;
  box-shadow: 10px 10px 15px #c3e7fd, -10px -10px 15px#c3e7fd;
}
.card__container {
  display: grid;
  width: 100%;
  height: 100%;
  grid-template-columns: 1fr 1fr 1fr;
  -webkit-align-items: center;
          align-items: center;
  justify-items: center;
  color: black;
}
.card__content {
  width: 90%;
  height: 95%;
  background: white;
  margin: 10px auto;
  border-radius: 5px;
  cursor: pointer;
  box-shadow: 10px 10px 15px #c3e7fd, -10px -10px 15px#c3e7fd;
  transition: 0.3s all ease-in-out;
}
.card__content:hover {
  margin-top: -10px;
}
.card__header {
  text-transform: uppercase;
  font-size: 20px;
  margin: 20px auto;
}

